import React from "react";
import { Helmet } from "react-helmet-async";
import { FaUsers, FaLaptopCode, FaCoffee, FaHeart } from "react-icons/fa";
import "./assets/css/style.css";

const Careers = () => {
  const benefits = [
    { icon: <FaUsers />, title: "Collaborative Culture", desc: "Work with smart, passionate people who love what they do." },
    { icon: <FaLaptopCode />, title: "Remote-First", desc: "Work from anywhere. We focus on output, not hours." },
    { icon: <FaCoffee />, title: "Continuous Learning", desc: "Stipends for courses, conferences, and books." },
    { icon: <FaHeart />, title: "Health & Wellness", desc: "Comprehensive health insurance and wellness programs." }
  ];

  const jobs = [
    { title: "Senior React Developer", type: "Full-time", location: "Remote", dept: "Engineering" },
    { title: "Backend Engineer (Node.js)", type: "Full-time", location: "Remote", dept: "Engineering" },
    { title: "UI/UX Designer", type: "Contract", location: "Remote", dept: "Design" },
    { title: "Technical Project Manager", type: "Full-time", location: "New York, NY", dept: "Product" }
  ];

  return (
    <div className="careers-page section-padding">
      <Helmet>
        <title>Careers | Join Quick Tech Solutions</title>
        <meta name="description" content="Join our team of innovators. We're hiring engineers, designers, and problem solvers to build the future of tech." />
      </Helmet>

      <div className="container">
        <div className="section-header">
          <span className="section-subtitle">Join Us</span>
          <h1 className="section-title">Build the Future With Us</h1>
          <p className="text-muted section-desc">
            We're looking for exceptional talent to help us solve complex problems and deliver amazing software.
          </p>
        </div>

        <div className="benefits-grid">
          {benefits.map((item, index) => (
            <div key={index} className="benefit-card">
              <div className="benefit-icon">{item.icon}</div>
              <h3>{item.title}</h3>
              <p className="text-muted">{item.desc}</p>
            </div>
          ))}
        </div>

        <div className="jobs-section">
          <h2 className="jobs-title">Open Positions</h2>
          <div className="jobs-list">
            {jobs.map((job, index) => (
              <div key={index} className="job-card">
                <div className="job-info">
                  <h3>{job.title}</h3>
                  <div className="job-meta">
                    <span>{job.dept}</span> &bull; <span>{job.type}</span> &bull; <span>{job.location}</span>
                  </div>
                </div>
                <button className="btn btn-outline btn-sm">Apply Now</button>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Careers;
